import React from 'react'
import styled from 'styled-components'
import { Text, Heading, Card, CardHeader, CardBody, Flex, Box } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import FoldableText from 'components/FoldableText'


const StyledCard = styled(Card)`
  flex: 1;
  height: fit-content;
`

// eslint-disable-next-line
const List = styled.ul`
  list-style-position: outside;
  padding: 0 24px;

  li {
    line-height: 1.5;
    margin-bottom: 4px;
  }

  li::marker {
    font-size: 12px;
  }
`
// eslint-disable-next-line
const FaqLi = styled.li`
  color: ${({ theme }) => theme.colors.textSubtle};
`

const FAQs = () => {
  const { t } = useTranslation()

  return (
    <Flex flexDirection="column">
      <Heading as="h1" scale="xxl" mb="40px">
        {t('FAQs')}
      </Heading>
      <Flex flexDirection={['column', null, null, null, 'row']}>
        <StyledCard mr={[null, null, null, null, '24px']} mb={['24px', null, null, null, '0']}>
          <CardHeader>
            <Heading>{t('')}</Heading>
          </CardHeader>
          <CardBody p="0 24px 24px">
            <FoldableText title={t('Is this project is safe?')} mt="24px">
              <Box display="inline">
                <Text display="inline" color="textSubtle">
                  {t('The purpose of the DTE is to help those that have been affected by other abandoned or scam projects. We have taken steps to ensure the project is safe such as locking liquidity and several contract audits.')}
                </Text>
              </Box>
            </FoldableText>
            <FoldableText title={t('How much DTE must be held in order to swap dead tokens?')} mt="24px">
              <Box color="textSubtle">
                <Text color="textSubtle" mb="4px">
                  {t('Holders must have a minimum of 200,000 DTE tokens in their wallet in order to utilize the DTE swap.')}
                </Text>
              
              </Box>
            </FoldableText>
            <FoldableText title={t('Why is there a 24 hr waiting period to sell swapped DTE?')} mt="24px">
              <Text color="textSubtle">
                {t(
                  'The 24 hr waiting period only applies to DTE that is acquired from swapping Dead tokens. This is a safety measure to protect the price stability of the exchange from dumps',
                )}
              </Text>
           
            </FoldableText>
            <FoldableText title={t('What are partner projects?')} mt="24px">
              <Text color="textSubtle">
                {t('Our partner projects are vetted recommendations for our community members. These projects are thoroughly screened and researched prior to listing on our site.')}
              </Text>
             
             
             
            </FoldableText>
          </CardBody>
        </StyledCard>
      </Flex>
    </Flex>
  )
} 

export default FAQs
