import React from 'react'
import styled from 'styled-components'
import { Text, Heading,  Flex, LinkExternal, Button } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'




// eslint-disable-next-line
const List = styled.ul`
  list-style-position: outside;
  padding: 0 0px;

  li {
    line-height: 1.5;
    margin-bottom: 4px;
  }

  li::marker {
    font-size: 12px;
  }
`
const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 16px;

  ${({ theme }) => theme.mediaQueries.md} {
    justify-content: space-between;
    flex-direction: row;
  }
`

const BubbleWrapper = styled(Flex)`
  svg {
    fill: ${({ theme }) => theme.colors.textSubtle};
    transition: background-color 0.2s, opacity 0.2s;
  }
  &:hover {
    svg {
      opacity: 0.65;
    }
  }
  &:active {
    svg {
      opacity: 0.85;
    }
  }
`


const FAQs = () => {
  const { t } = useTranslation()

  return (
    <Wrapper>
    <Flex flexDirection="column" >
      <Heading as="h6" scale="xl"  display="center">
        {t('Dead Token Exchange')}
      </Heading>
      <Flex flexDirection={['column', null, null, null, 'row']}>


 <section>


 <Flex display="center" >
              <Heading  color="white" marginBottom="20px" marginTop="40px" > {t('About Us')}</Heading>
        </Flex>
   
        <Text color="textSubtle">
                  {t('The Dead Token Exchange allows the exchange of those failed project tokens for')}
                </Text>
                <Text color="textSubtle">
                  {t('DTE tokens that can be held for long term growth and value, exchanged for our')}
                </Text>
                <Text color="textSubtle">
                  {t('for our vetted partner projects or sold to recover those losses.')}
                </Text>
                <Text color="transparent">
                  {t('abandoned or scam projects. We have taken steps to ensure the project is safe such as locking liquidity and several contract audits.')}
                </Text>
                <Text color="transparent">
                  {t('abandoned or scam projects. We have taken steps to ensure the project is safe such as locking liquidity and several contract audits.')}
                </Text>

 </section>

 
 <section>
 
   <Flex display="center" >
              <Heading color="white" marginBottom="20px" marginTop="40px" > {t('SOCIAL')}</Heading>
        </Flex>
      <Flex flexDirection={['row', 'row', 'row']} alignItems="center">
        <LinkExternal
          href="https://t.me/Deadtokenexchange"
          ml={[0, 0, 0]}
          mt={['20px', '20px', 0]}
          mb={['8px', '8px', 0]}
        >
          {t('Join Our Telegram')}
        </LinkExternal>
      </Flex>
      <Flex flexDirection={['row', 'row', 'row']} alignItems="center">
        <LinkExternal
          href="https://twitter.com/DeadTokenSwap"
          ml={[0, 0, 0]}
          mt={['20px', '20px', 0]}
          mb={['8px', '8px', 0]}
        >
          {t('Follow Us on Twitter')}
        </LinkExternal>
      </Flex>
      <Flex flexDirection={['row', 'row', 'row']} alignItems="center">
        <LinkExternal
          href="#"
          ml={[0, 0, 0]}
          mt={['20px', '20px', 0]}
          mb={['8px', '8px', 0]}
        >
          {t('Join Our Discord')}
        </LinkExternal>
      </Flex>
      <Flex flexDirection={['row', 'row', 'row']} alignItems="center">
        
        <LinkExternal
          href="https://www.reddit.com/r/deadtokenexchange/"
          ml={[0, 0, 0]}
          mt={['20px', '20px', 0]}
          mb={['8px', '8px', 0]}
        >
          {t('Join Our Reddit')}
        </LinkExternal>
      </Flex>

 </section>

    </Flex>
   
      <Flex
        flexGrow={1}
        alignItems="center"
        width={['100%', '100%', '100%', 'auto']}
        justifyContent={['center', 'center', 'center', 'flex-end']}
      >
        <BubbleWrapper>
          <Button
            id=""
            as="a"
            external
            href="/swap"
            variant="subtle"
          >
            {t('Documentation')}
          </Button>
        </BubbleWrapper>
      </Flex>
      </Flex>
      <Wrapper>
      <Flex flexDirection={['column', 'column', 'row']} alignItems="center">
      <Flex >
            
            <Heading display="Center" font-size="20px" color="white" marginTop="4px"> {t('Copyright@ DeadTokenExchnage | All Rights Reserved')}</Heading>
      </Flex>
      </Flex>


     
      <Flex
        flexGrow={1}
        alignItems="center"
        width={['100%', '100%', '100%', 'auto']}
        justifyContent={['center', 'center', 'center', 'flex-end']}
      >
        <BubbleWrapper>
          <Button
            id="clickExchangeHelp"
            as="a"
            external
            href="https://t.me/Deadtokenexchange"
            variant="subtle"
          >
            {t('Need help ?')}
          </Button>
         
        </BubbleWrapper>
      </Flex>
    
    </Wrapper>
    </Wrapper>
  )
} 

export default FAQs
