import React from 'react'
import styled from 'styled-components'
// eslint-disable-next-line
import { Box, Flex, Text,  Heading, Image } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'


const StepContainer = styled(Flex)`
  gap: 24px;
  width: 100%;
  flex-direction: column;
  ${({ theme }) => theme.mediaQueries.md} {
    flex-direction: row;
  }
`


const StyledStepCard = styled(Box)`
  display: flex;
  align-self: baseline;
  position: relative;
  background: ${({ theme }) => theme.colors.cardBorder};
  padding: 1px 1px 3px 1px;
  border-radius: ${({ theme }) => theme.radii.card};
`

const StepCardInner = styled(Box)`
  width: 100%;
  padding: 24px;
  color: Blue;
  background: ${({ theme }) => theme.card.background};
  border-radius: ${({ theme }) => theme.radii.card};
`


type Step = { title: string; subtitle: string; label: string  }

const StepCard: React.FC<{ step: Step }> = ({ step }) => {
  return (
    <StyledStepCard width="100%">
      <StepCardInner height={['280px', '180px', null, '300px']}>
        <Text mb="16px" fontSize="18px" bold textAlign="right" textTransform="uppercase">
          {step.label}
        </Text>
        <Heading mb="16px" scale="lg" color="secondary">
          {step.title}
        </Heading>
        <Text color="textSubtle">{step.subtitle}</Text>
      </StepCardInner>
    </StyledStepCard>
  )
}



const HowToPlay: React.FC = () => {
  const { t } = useTranslation()

  const steps: Step[] = [
    {

  
      label: t('', { number: 1 }),
      
      title: t('Development 3%'),
      subtitle: t('The Dead Token Exchange will use developmental funds for marketing purposes, operating expenses and the addition of partner projects to the DTE ecosystem'),
    },
    {
      label: t('', { number: 2 }),
      title: t('DTE Vault 2%'),
      subtitle: t('The DTE Vault is an integral part of the Dead Token Exchange ecosystem,as it provides liquidity to facilitate the exchange of dead or unwanted tokens for DTE tokens.A portion of the Vault fee will also be used for investments thoroughly vetted projects at the private & presale stages.'),
    },
  
  ]
  return (
    <Box width="100%" backgroundImage="logo.png"  >
      <Flex mb="40px" alignItems="center" flexDirection="column">
        <Heading mb="24px" scale="xl" color="secondary">
          {t('Tokenomics')}
        </Heading>
        <Text textAlign="center">
          {t(
            '',
          )}
        </Text>
        <Text>{t('')}</Text>
      </Flex>
      <StepContainer>
        {steps.map((step) => (
          <StepCard key={step.label} step={step} />
        ))}
      </StepContainer>
    </Box>
  )
}

export default HowToPlay
