import React from 'react'
import { Heading, Flex, Text } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'

// Values fetched from bitQuery effective 6/9/21

const Stats = () => {
  const { t } = useTranslation()
 
  return (
    <Flex justifyContent="left" alignItems="left" flexDirection="column">
  
      <Heading textAlign="left" scale="xl" marginBottom="4%">
        {t('Purpose')}
      </Heading>
      
      <Text textAlign="left" color="textSubtle">
        {t('Investors are often left holding the bag when DeFi projects do not go as planned. Of course, we should always do our own research but there is still a chance things will not work out as we hope.')}
      </Text>

      <Text textAlign="left" color="textSubtle">
        {t('Even projects that seem stable can sometimes come crashing down (see Luna)')}
      </Text>
  
      <Text textAlign="left" color="textSubtle">
        {t('The Dead Token Exchange allows holders of low value, unsellable or unwanted tokens to exchange them for DTE tokens. DTE tokens can then be held for long term value, staked to earn interest or swapped for BNB (24 hour cool down for BNB swap).')}
      </Text>
  
    </Flex>
  )
}

export default Stats
